class App extends React.Component {
  state = {
    result: null,
    price: ""
  };
  componentDidMount() {
    fetch("https://api.privatbank.ua/p24api/pubinfo?json&exchange&coursid=5")
      .then(response => response.json())
      .then(data => {
        const usd = data.find(money => money.ccy === "USD");
        this.setState({ usd });
      });
  }
  handleChangeCapacity = capacity => {
    this.setState({ capacity });
  };
  handleChangeState = state => {
    this.setState({ state });
  };
  handleAuctionChange = auction => {
    this.setState({ auction });
  };
  handleChangeYear = year => {
    const newYear = newDate - year.value;
    year = {
      value: newYear,
      label: year.label
    };
    this.setState({ year });
  };
  handleChangeType = type => {
    this.setState({ type });
  };
  handleInputChange = event => {
    let { value } = event.target;
    value = value.replace(/[^0-9]/g, "");
    this.setState({
      price: value
    });
  };
  getBetween = (price, mainTax, secondTax, thirdTax) => {
    let sumTax;
    let salePrice;
    const between = (x, min, max) => {
      return x >= min && x <= max;
    };
    let tax = mainTax.find(item => {
      if (between(price, item.from, item.to)) {
        return item;
      }
      return false;
    });
    const nonKiosk = secondTax.find(item => {
      if (between(price, item.from, item.to)) {
        return item;
      }
      return false;
    }).fee;
    if (thirdTax) {
      salePrice = thirdTax.find(item => {
        if (between(price, item.from, item.to)) {
          return item;
        }
        return false;
      }).fee;
    }
    if (tax.fee === 0.04) {
      tax.fee = price * tax.fee;
    }
    if (tax.bonusFee) {
      tax.fee = tax.fee + price * tax.bonusFee;
    }
    if (thirdTax) {
      sumTax = tax.fee + nonKiosk + salePrice;
    } else {
      sumTax = tax.fee + nonKiosk;
    }
    return sumTax;
  };
  getRozmutnennya = () => {
    const { capacity, year, type, price, auction } = this.state;
    let cof;
    let sumTax;
    let excise;
    if (type.value === "Disel" && capacity.value <= "3.5") {
      cof = 75;
    } else if (type.value === "Disel" && capacity.value > "3.5") {
      cof = 150;
    } else if (type.value === "Oil" && capacity.value <= "3") {
      cof = 50;
    } else if (type.value === "Oil" && capacity.value > "3") {
      cof = 100;
    }
    if (auction.value === "Copart") {
      sumTax = this.getBetween(
        price,
        copartTax,
        copartSecondTax,
        copartThirdTax
      );
    } else {
      sumTax = this.getBetween(price, iaaiTax, iaaiSecondTax);
    }
    if (Number(year.value) === 0 || Number(year.value) === -1) {
      excise = cof * Number(capacity.value) * 1.12;
    } else {
      excise = cof * Number(capacity.value) * Number(year.value) * 1.12;
    }
    const mutnaVartist = Number(price) + sumTax + 450;
    const muto = mutnaVartist * 0.1;
    const pdv = (muto + excise + mutnaVartist) * 0.2;
    let rozmutnennya = excise + muto + pdv;
    rozmutnennya = Math.round(rozmutnennya);
    this.setState({ rozmutnennya });
  };
  getPensiynuiFond = () => {
    const { price, usd, auction } = this.state;
    let sumTax;
    if (auction.value === "Copart") {
      sumTax = this.getBetween(
        price,
        copartTax,
        copartSecondTax,
        copartThirdTax
      );
    } else {
      sumTax = this.getBetween(price, iaaiTax, iaaiSecondTax);
    }
    const mutnaVartist = Number(price) + sumTax + 450;
    const convertedMutnaVartist = mutnaVartist * usd.buy;
    let fond;
    if (convertedMutnaVartist <= 319440) {
      fond = mutnaVartist * 0.03;
    } else if (convertedMutnaVartist <= 561440) {
      fond = mutnaVartist * 0.04;
    } else {
      fond = mutnaVartist * 0.05;
    }
    fond = Math.round(fond);
    this.setState({
      fond
    });
  };
  render() {
    const {
      capacity,
      rozmutnennya,
      state,
      year,
      type,
      price,
      fond,
      auction
    } = this.state;
    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
        </header>
        <div className="content">
          <div className="label">Аукціон</div>
          <Select
            value={auction}
            onChange={this.handleAuctionChange}
            options={auctionOptions}
            className="select"
          />
          <div className="label">Вартість автомобіля</div>
          <input
            type="text"
            className="input"
            value={price}
            onChange={this.handleInputChange}
            placeholder="Type..."
          />
          <div className="label">Тип двигуна</div>
          <Select
            value={type}
            onChange={this.handleChangeType}
            options={typeOptions}
            className="select"
          />
          <div className="label">Рік випуску</div>
          <Select
            value={year}
            onChange={this.handleChangeYear}
            options={yearOptions}
            className="select"
          />
          <div className="label">Об'єм двигуна, куб.дм</div>
          <Select
            value={capacity}
            onChange={this.handleChangeCapacity}
            options={capacityOptions}
            className="select"
          />
          <div className="label">Штат</div>
          <Select
            value={state}
            onChange={this.handleChangeState}
            options={stateOptions}
            className="select"
          />
        </div>
        <div className="buttonWrapper">
          <div className="buttonBlock">
            <button
              type="text"
              className="button"
              onClick={this.getRozmutnennya}
            >
              Розрахувати розмитнення:
            </button>
            {rozmutnennya}
          </div>
          <div className="buttonBlock">
            <button
              type="text"
              className="button"
              onClick={this.getPensiynuiFond}
            >
              Розрахувати пенсійний фонд:
            </button>
            {fond}
          </div>
        </div>
      </div>
    );
  }
}

export default App;
